.Form {
  display: flex;
  flex-direction: column;
  align-items: flex-start !important;
  padding-top: 80px;
  padding-left: 20px;
  padding-right: 20px !important;
  width: calc(100% - 517px);

  margin-bottom: 69px;

  @media screen and (min-width: 768px) {
    align-items: center;
    margin-top: 100px;
    padding-top: 0;
    padding-left: 32px;
    padding-right: 0;
  }

  @media screen and (min-width: 1280px) {
    margin-top: 60px;
    // padding-left: 16px;
  }

  @media screen and (max-width: 768px) {
    flex-direction: column;
    width: 100%;
    margin-top: 80px;
    margin-bottom: 60px;
    padding: 0 20px;
  }
}
.FieldProduct {
  position: relative;
  width: 100%;

  @media screen and (min-width: 768px) {
    margin-right: 22px;
    width: 240px;
  }
}

.FieldWeight {
  position: relative;
  width: 100%;
  margin-top: 32px;

  @media screen and (min-width: 768px) {
    margin-top: 0;
    margin-right: 67px;
    width: 107px;
  }

  @media screen and (min-width: 1280px) {
    margin-right: 60px;
  }
}

.Label {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  transition: 250ms cubic-bezier(0.4, 0, 0.2, 0.3);
  font-family: 'Verdana';
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.04em;
  color: #9b9faa;
}

.Input {
  display: inline-block;
  height: 48px;
  width: 100%;
  border: none;
  border-bottom: 1px solid #e0e0e0;
  color: #212121;
  outline: none;
  margin-bottom: 48px;

  &:hover,
  &:focus {
    border-color: #fc842d;
  }

  &:focus ~ label,
  &:valid ~ label {
    top: 0px;
  }

  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  &[type='number'] {
    -moz-appearance: textfield;
  }
}

.LabelWeight {
  left: 0;

  @media screen and (min-width: 768px) {
    left: auto;
    right: 0;
  }
}

.LabelTitle {
  left: 0;
}

.InputWeight {
  @media screen and (min-width: 768px) {
    text-align: right;
  }

  text-align: left;
}

.button {
  margin-top: 0;
  padding: 0px;
  width: 48px;
  height: 48px;
  background-color: #fc842d;
  color: #ffffff;
  border: none;
  border-radius: 50%;
  box-shadow: 0px 4px 10px rgba(252, 132, 45, 0.3);
  outline: none;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 0.3),
    color 250ms cubic-bezier(0.4, 0, 0.2, 0.3);
  cursor: pointer;

  &:enabled:hover,
  &:enabled:focus {
    color: #fc842d;
    background-color: #ffffff;
    border: 2px solid #fc842d;
    box-shadow: none;
  }

  &:disabled {
    background-color: #fc842d50;
    box-shadow: none;
  }

  & svg {
    fill: currentColor;
  }
}



.btnMobil {
  margin-bottom: 60px;
}

